<template>
  <div class="page-container">
    <div class="page-header">
      <h1>🤖 AI Agents Launcher</h1>
      <p class="subtitle">Create your own AI Agent for the BITCAT Universe or for your own Telegram group</p>
    </div>
    <!-- Agent Carousel -->
    <div class="agent-carousel">
      <transition name="fade" mode="out-in">
        <div class="carousel-slide" :key="currentSlide">
          <AiAgentCard v-bind="sampleAgents[currentSlide]" @more-info="handleMoreInfo(sampleAgents[currentSlide])" />
        </div>
      </transition>
      <div class="carousel-indicators">
        <button 
          v-for="(_, index) in sampleAgents" 
          :key="index"
          class="indicator"
          :class="{ active: currentSlide === index }"
          @click="setSlide(index)"
        ></button>
      </div>
    </div>
    
    <div class="create-agent-page">

      <div class="create-form">
        <!-- Basic Information -->
        <div class="form-section">
