          <h2 class="warning-message" v-if="!store.getters.isAuthenticated">Your need to be logged in to create an AI Agent. <router-link to="/my" class="more-info">Login →</router-link></h2>

          <div class="avatar-preview" @click="showAvatarPopup = true">
            <img :src="prepareAvatarUrl(selectedAvatar)" alt="Agent Avatar">
            <div class="avatar-overlay">Change Avatar</div>
