                {{ showConnectionOptions ? 'Hide Options' : 'Other Connection Options' }}
              </button>
              
              <div class="options-content" v-if="showConnectionOptions">
                <div class="form-group">
                  <label>Your Telegram bot API Key</label>
                  <input 
                    v-model="agentData.apiKey" 
                    type="text" 
                    placeholder="API Key is optional"
                  >
                  <span class="hint hint-last">If you'd like to connect your AI Agent with your Telegram bot. With zero code
                    <span class="error-message" v-if="errors.apiKey">. {{ errors.apiKey }}</span></span>
                  
                  <input 
                    v-model="agentData.botUsername" 
                    type="text" 
                    placeholder="Bot username without @"
                  >
                  <span class="hint">Required if you provide an API key
                    <span class="error-message" v-if="errors.botUsername">. {{ errors.botUsername }}</span></span>
                </div>
                
                <p class="options-description">
                  <strong>Important</strong><br>
                  If you don't want to create a separate Telegram bot and provide an API key, you can specify groups (up to 5) where you plan to use your agent. In this case, the API key will be ignored.
                </p>
                <p class="options-description">
                  After creation, you can add @BitcatAiBot as an administrator to your group, and it will respond to messages according to your prompt.
                </p>
                
                <div class="form-group form-group_last">
                  <label>Telegram Groups</label>
                  <input 
                    v-model="agentData.groups"
                    type="text"
                    placeholder="Enter group names separated by commas (e.g., group1, group2)"
                    @input="validateGroups"
                  >
                  <span class="hint">Up to 5 groups, separated by commas. Without @</span>
                  <span class="error-message" v-if="errors.groups">{{ errors.groups }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- Agent Prompt Template -->
        <div class="form-section">
          <h2>Agent Prompt</h2>
          <div class="form-group form-group_last prompt-template">
            <div class="prompt-editor">
              <p class="prompt-line">
                You are a<small> (required)</small> <span class="prompt-input" contenteditable @blur="updatePrompt('role')" @input="checkLength($event, 'role', 100)" :data-placeholder="'e.g., crypto trading expert, NFT curator, blockchain analyst'"></span>
                <span class="char-counter" :class="{ 'near-limit': promptData.role.length > 80 }">({{ promptData.role.length }}/100)</span>
              </p>
              <span class="error-message" v-if="errors.role">{{ errors.role }}</span>
              
              <p class="prompt-line">
                Your communication style is <span class="prompt-input" contenteditable @blur="updatePrompt('style')" @input="checkLength($event, 'style', 50)" :data-placeholder="'e.g., professional and friendly, casual but informative'"></span>
                <span class="char-counter" :class="{ 'near-limit': promptData.style.length > 40 }">({{ promptData.style.length }}/50)</span>
              </p>
              
              <p class="prompt-line">Your main task</p>
              <div class="prompt-tasks">
                <p class="prompt-line">
                  <span class="prompt-input" contenteditable @blur="updatePrompt('task1')" @input="checkLength($event, 'task1', 400)" :data-placeholder="'Enter primary task. Write what task the agent should perform'"></span>
                  <span class="char-counter" :class="{ 'near-limit': promptData.task1.length > 320 }">({{ promptData.task1.length }}/400)</span>
                </p>
              </div>

              <p class="prompt-line">
                Your expertise areas include <span class="prompt-input" contenteditable @blur="updatePrompt('expertise')" @input="checkLength($event, 'expertise', 100)" :data-placeholder="'e.g., DeFi, NFTs, blockchain gaming'"></span>
                <span class="char-counter" :class="{ 'near-limit': promptData.expertise.length > 80 }">({{ promptData.expertise.length }}/100)</span>
              </p>
              
              <p class="prompt-line">
                Additional context <span class="prompt-input" contenteditable @blur="updatePrompt('context')" @input="checkLength($event, 'context', 300)" :data-placeholder="'Any other relevant information about your agent'"></span>
                <span class="char-counter" :class="{ 'near-limit': promptData.context.length > 240 }">({{ promptData.context.length }}/300)</span>
              </p>
            </div>

            <div class="prompt-preview">
              <h3>Prompt Preview <button class="toggle-preview" @click="showPreview = !showPreview">{{ showPreview ? 'Hide' : 'Show' }}</button></h3>
              <div class="preview-content" v-if="showPreview">{{ generatedPrompt }}</div>
            </div>
          </div>
        </div>

        <!-- Advanced Settings -->
        <div class="form-section">
          <h2>Advanced Settings</h2>
          <div class="form-group">
            <label>Response Length</label>
            <div class="slider-container">
              <input 
                type="range" 
                v-model="agentData.settings.responseLength" 
                min="25" 
                max="250" 
                step="25"
              >
              <span>{{ agentData.settings.responseLength }} tokens</span>
            </div>
            <span class="setting-hint">Controls how long the agent's responses will be. Lower values (25-100) for concise answers, higher values (150-250) for more detailed explanations.</span>
          </div>

          <div class="form-group form-group_last">
            <label>Temperature</label>
            <div class="slider-container">
              <input 
                type="range" 
                v-model="agentData.settings.temperature" 
                min="0.1" 
                max="1.5" 
                step="0.1"
              >
              <span>{{ agentData.settings.temperature }}</span>
            </div>
            <span class="setting-hint">Controls the creativity of responses. Lower values (0.1-0.3) for more focused and deterministic responses, higher values (0.7-1.5) for more creative and varied outputs. Recommended values are between 0.5 and 1.0.</span>
          </div>
        </div>

        <!-- Credits policy -->
        <div class="form-section credits-policy">
          <h2>Credits Policy</h2>
          <div class="policy-content">
            <div class="policy-preview" :class="{ expanded: showFullPolicy }">
              <p><strong>🎁 Currently FREE for Everyone!</strong> During our promotional period, all agent interactions are completely free. To create an agent and make it available, you simply need to be a holder of 100,000 BITCAT tokens for each new agent.</p>
              
              <div class="policy-details" v-if="showFullPolicy" :class="{ visible: showFullPolicy }">
                <h3>How Credits Work:</h3>
                <ul>
                  <li>
                    <strong>Free Within BITCAT:</strong> Using your agent inside the BITCAT Universe is free. Your agent will be publicly available to everyone thanks to the Agent Tag(your_tag_agent) both on the site(AI agents list) and in the telegram(@BitcatAiBot your_tag_agent Hello) group
                  </li>
                  <li>
                    <strong>External Usage:</strong> Connecting your agent to external platforms (Telegram groups, Twitter*) requires credits. You can buy credits for BITCAT tokens.
                    <ul>
                      <li>Each interaction outside BITCAT Universe consumes credits</li>
                      <li>Credits must be available in your account before external usage</li>
                      <li>*Twitter integration coming soon</li>
                    </ul>
                  </li>
                  <li>
                    <strong>Future Plans:</strong> Credits will become a paid feature after the promotional period
                  </li>
                </ul>
              </div>
            </div>
            
            <button class="toggle-policy" @click="showFullPolicy = !showFullPolicy">
              {{ showFullPolicy ? 'Show Less' : 'Show More' }}
            </button>
          </div>
        </div>

        <div class="form-actions">
          <button v-if="store.getters.isAuthenticated"
            class="create-button" 
            @click="createAgent"
            :disabled="!isFormValid || isCreating"
          >
            {{ isCreating ? 'Creating...' : 'Create Agent' }}
          </button>
          <router-link v-else to="/my" class="create-button">
            Login first
          </router-link>
          <span class="error-message" v-if="errors.general">{{ errors.general }}</span>
        </div>
      </div>

      <!-- Avatar Selection Popup -->
      <div class="popup" v-if="showAvatarPopup">
        <div class="popup-content">
          <h3>AI Agent Avatars for Universe</h3>
          <div class="avatar-grid">
            <div 
              v-for="avatar in 26" 
              :key="avatar"
              class="avatar-option"
              :class="{ selected: selectedAvatar === avatar }"
              @click="selectAvatar(avatar)"
            >
              <img :src="prepareAvatarUrl(avatar)" :alt="`avatar #${avatar}`">
            </div>
          </div>
          <button class="close-button" @click="showAvatarPopup = false">Close</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'
import axios from 'axios'
import store from '@/vuex'
import AiAgentCard from '@/components/ai/AiAgentCard.vue'

const router = useRouter()

// State
const showAvatarPopup = ref(false)
const selectedAvatar = ref(1)
const newKnowledgeArea = ref('')
const isCreating = ref(false)
const errors = reactive({
  name: '',
  tag: '',
  apiKey: '',
  botUsername: '',
  groups: '',
  role: '',
  general: ''
})

// Add to your reactive data
const showFullPolicy = ref(false)
const showConnectionOptions = ref(false)
const showPreview = ref(false)

// Add to script section
const promptData = reactive({
  role: '',
  style: '',
  task1: '',
  task2: '',
  task3: '',
  expertise: '',
  context: ''
})

const updatePrompt = (field) => {
  const input = event.target
  promptData[field] = input.textContent.trim()
  
  // Reset placeholder if empty
  if (!promptData[field]) {
    input.textContent = ''
  }
}

const checkLength = (event, field, maxLength) => {
  const input = event.target
  const text = input.textContent.trim()
  if (text.length > maxLength) {
    input.textContent = text.substring(0, maxLength)
  }
  promptData[field] = input.textContent.trim()
}

const generatedPrompt = computed(() => {
  let prompt = `You are a ${promptData.role || '[role]'}.`
  
  if (promptData.style) {
    prompt += `Your communication style: ${promptData.style}. `
  }
  
  prompt += '\n\nYour main task:\n'
  if (promptData.task1) prompt += `- ${promptData.task1}\n`
  
  // prompt += '\nYou are part of the BITCAT Universe, a Community Takeover project on the Solana blockchain. '
  
  if (promptData.expertise) {
    prompt += `\n\nYour expertise areas include: ${promptData.expertise}. `
  }
  
  if (promptData.context) {
    prompt += `\n\nAdditional context: ${promptData.context}`
  }
  
  prompt += '\n\nFormat: text/plain. Language: respond in the language of the query. Output: Always provide your response in plain text, without any formatting, markdown symbols, or line breaks (\\n). Respond directly and concisely with plain text output. Do not use symbols like **, or additional line breaks in your answers.'
  
  return prompt
})

// Sample agents data
const sampleAgents = [
  {
    avatar: 3,
    name: "BitTrader Pro",
    agentTag: "bittrader_pro_agent",
    description: "Expert crypto trading assistant with real-time market analysis and portfolio management.",
    userTag: "bitcat",
    isDisabled: true
  },
  {
    avatar: 7,
    name: "NFT Curator",
    agentTag: "nft_curator_agent",
    description: "Your personal guide in the NFT world. Discovers rare gems and analyzes market trends.",
    userTag: "bitcat",
    isDisabled: true
  },
  {
    avatar: 12,
    name: "DeFi Wizard",
    agentTag: "defi_wizard_agent",
    description: "Smart DeFi strategist helping you navigate yield farming and liquidity pools.",
    userTag: "bitcat",
    isDisabled: true
  },
  {
    avatar: 15,
    name: "Chain Guardian",
    agentTag: "chain_guardian_agent",
    description: "Blockchain security expert monitoring smart contracts and transactions.",
    userTag: "bitcat",
    isDisabled: true
  },
  {
    avatar: 19,
    name: "MetaVerse Guide",
    agentTag: "meta_guide_agent",
    description: "Your companion in exploring and building in the metaverse.",
    userTag: "bitcat",
    isDisabled: true
  },
  {
    avatar: 23,
    name: "Web3 Sage",
    agentTag: "web3_sage_agent",
    description: "Decentralized web expert helping with dApps and web3 development.",
    userTag: "bitcat",
    isDisabled: true
  }
]

// Carousel logic
const currentSlide = ref(0)
let carouselInterval

const setSlide = (index) => {
  currentSlide.value = index
}

const nextSlide = () => {
  currentSlide.value = (currentSlide.value + 1) % sampleAgents.length
}

const handleMoreInfo = (agent) => {
  console.log('More info for:', agent.agentTag)
}

onMounted(() => {
  carouselInterval = setInterval(nextSlide, 5000)
})

onUnmounted(() => {
  clearInterval(carouselInterval)
})

// Form data
const agentData = reactive({
  name: '',
  tag: '',
  description: '',
  avatar: '',
  role: 'assistant',
  traits: {
    formal: false,
    friendly: true,
    professional: false,
    casual: false
  },
  knowledgeAreas: [],
  settings: {
    responseLength: 150,
    temperature: 0.7
  },
  apiKey: '',
  botUsername: '',
  groups: ''
})

// Validation
const validateName = () => {
  if (agentData.name.length < 3) {
    errors.name = 'Name must be at least 3 characters long'
  } else if (agentData.name.length > 50) {
    errors.name = 'Name must be less than 50 characters'
  } else {
    errors.name = ''
  }
}

const validateTag = () => {
  const tagRegex = /^[a-z0-9_]+$/
  if (!tagRegex.test(agentData.tag)) {
    errors.tag = 'Tag can only contain lowercase letters, numbers and underscores'
  } else {
    errors.tag = ''
  }
}

const validateGroups = () => {
  const groups = agentData.groups.split(',')
  if (groups.length > 5) {
    errors.groups = 'You can specify up to 5 groups'
  } else {
    errors.groups = ''
  }
}

const validateForm = () => {
  let isValid = true
  errors.general = ''

  // Reset all errors
  Object.keys(errors).forEach(key => errors[key] = '')

  // Name validation
  if (!agentData.name.trim()) {
    errors.name = 'Name is required'
    isValid = false
  } else if (agentData.name.length < 3) {
    errors.name = 'Name must be at least 3 characters long'
    isValid = false
  }

  // Tag validation
  if (!agentData.tag.trim()) {
    errors.tag = 'Tag is required'
    isValid = false
  } else if (!/^[a-z0-9_]+agent$/.test(agentData.tag)) {
    errors.tag = 'Tag must contain only lowercase letters, numbers, underscores and end with "agent"'
    isValid = false
  }

  // API Key or Groups validation
  if (agentData.apiKey && !/^[0-9]+:[A-Za-z0-9_-]+$/.test(agentData.apiKey)) {
    errors.apiKey = 'Invalid API Key format'
    isValid = false
  }

  if (agentData.botUsername && !/^[a-zA-Z0-9_]+$/.test(agentData.botUsername)) {
    errors.botUsername = 'Invalid bot username format'
    isValid = false
  }

  if (agentData.groups) {
    const groupsList = agentData.groups.split(',').map(g => g.trim())
    if (groupsList.length > 5) {
      errors.groups = 'Maximum 5 groups allowed'
      isValid = false
    }
  }

  // Only role is required in prompt
  if (!promptData.role.trim()) {
    errors.role = 'Role is required'
    isValid = false
  }

  return isValid
}

// Computed
const isFormValid = computed(() => {
  return agentData.name.length >= 3 &&
    agentData.tag.length > 0 &&
    agentData.description.length > 0 &&
    selectedAvatar.value &&
    !errors.name &&
    !errors.tag &&
    !errors.groups
})

const prepareAvatarUrl = (avatarNum) => {
  return `https://solanabitcat.com/data/ai-agents/bitcat-ai-agent-avatar-${avatarNum}.png`
}

// Methods
const selectAvatar = (avatarNum) => {
  selectedAvatar.value = avatarNum
  agentData.avatar = avatarNum
  showAvatarPopup.value = false
}

const addKnowledgeArea = () => {
  if (newKnowledgeArea.value.trim() && !agentData.knowledgeAreas.includes(newKnowledgeArea.value)) {
    agentData.knowledgeAreas.push(newKnowledgeArea.value.trim())
    newKnowledgeArea.value = ''
  }
}

const removeKnowledgeArea = (index) => {
  agentData.knowledgeAreas.splice(index, 1)
}

const createAgent = async () => {
  if (!validateForm()) {
    errors.general = 'Please fix the errors before submitting'
    return
  }

  isCreating.value = true

  try {
    // Prepare agent data
    const agentPayload = {
      name: agentData.name.trim(),
      tag: agentData.tag.trim(),
      description: agentData.description.trim(),
      avatar: selectedAvatar.value,
      apiKey: agentData.apiKey.trim(),
      botUsername: agentData.botUsername.trim(),
      groups: agentData.groups ? agentData.groups.trim() : '',
      prompt: {
        role: promptData.role.trim(),
        style: promptData.style.trim(),
        task1: promptData.task1.trim(),
        expertise: promptData.expertise.trim(),
        context: promptData.context.trim()
      },
      settings: {
        temperature: parseFloat(agentData.settings.temperature),
        responseLength: parseInt(agentData.settings.responseLength)
      }
    }

    const response = await axios.post('/ai/agent', agentPayload)

    console.log('Response:', response.data)

    if (response.data.success) {
      console.log('Agent created successfully')
      // TODO: Show success notification
      // router.push('/agents')
    } else {
      throw new Error(response.data.error || 'Failed to create agent')
    }

  } catch (error) {
    console.error('Error creating agent:', error)
    errors.general = error.response?.data?.error || error.message || 'Failed to create agent. Please try again.'
  } finally {
    isCreating.value = false
  }
}
</script>

