<template>
  <div class="agent-card" :class="{ 'disabled': isDisabled }">
    <div class="card-inner">
      <div class="card-front">
        <div class="avatar-container">
          <div class="avatar-glow"></div>
          <img :src="prepareAvatarUrl(avatar)" :alt="name" class="avatar">
          <div class="level-badge"><i class="fas fa-chart-line"></i> {{ used }}</div>
        </div>
        <div class="agent-info">
          <h3 class="name">{{ name }}</h3>
          <div class="tag">{{ agentTag }}</div>
          <p class="description">{{ description }}</p>
          <div class="creator">Created by <span>{{ userTag }}</span></div>
        </div>
        <div class="card-actions">
          <button @click="openAgent(agentTag)" class="card-btn" :disabled="isDisabled">
            <span class="btn-text">Profile</span>
          </button>
          <button 
            class="card-btn primary" 
            @click="useAgent"
            :disabled="isDisabled"
          >
            <i class="fas fa-comments"></i>
            <span class="btn-text">Chat</span>
          </button>
        </div>
      </div>
    </div>
    <div class="card-effects">
      <div class="glow-effect"></div>
      <div class="particles"></div>
    </div>
  </div>
</template>

<script setup>
import { defineProps, defineEmits, ref } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

const router = useRouter()

const store = useStore()
const isUsed = ref(false)

const props = defineProps({
  avatar: {
    type: [String, Number],
    required: true
  },
  name: {
    type: String,
    required: true
  },
  agentTag: {
    type: String,
    required: true
  },
  description: {
    type: String,
    required: true
  },
  userTag: {
    type: String,
    required: true
  },
  used: {
    type: String,
    required: false,
    default: '0'
  },
  
  isDisabled: {
    type: Boolean,
    default: false
  }
})

defineEmits(['more-info', 'use-agent'])

const openAgent = (agentTag) => {
  router.push(`/ai-agents/${agentTag}`)
}

const useAgent = () => {
  isUsed.value = true
  
  // Update agent info in store
  store.commit('UPDATE_AGENT_INFO', {
    name: props.name,
    agentTag: props.agentTag,
    avatar: props.avatar
  })
  
  // Сохраняем выбранного агента в localStorage
  localStorage.setItem('selectedAgent', JSON.stringify({
    name: props.name,
    agentTag: props.agentTag,
    // description: props.description,
    avatar: props.avatar
  }))
  
  // Получаем историю чата для этого агента
  let chatHistory = JSON.parse(localStorage.getItem('chatHistory') || '{}')
  
  // Если нет истории для этого агента, создаем первое сообщение с описанием
  if (!chatHistory[props.agentTag]) {
    chatHistory[props.agentTag] = [{
      type: 'agent',
      content: props.description,
      timestamp: new Date().toISOString()
    }]
    localStorage.setItem('chatHistory', JSON.stringify(chatHistory))
  }
  
  // Открываем чат с агентом
  store.commit('SET_CURRENT_AGENT', {
    name: props.name,
    agentTag: props.agentTag,
    // description: props.description,
    avatar: props.avatar
  })
  
  // Открываем окно чата
  store.commit('TOGGLE_AGENTS_LIST', false)
  store.commit('TOGGLE_CHAT', true)
}

const prepareAvatarUrl = (avatarNum) => {
  return `https://solanabitcat.com/data/ai-agents/bitcat-ai-agent-avatar-${avatarNum}.png`
}
</script>

